.dialogs .title {
    font-size: 30px;
    line-height: 35px;
    margin-bottom: 30px;
}

.dialogs .row {
    display: flex;
}

.dialogs .users {
    padding-right: 50px;
    border-right: 1px solid violet;
}

.dialogs .users .item {
    display: block;
    font-size: 24px;
    line-height: 26px;
    position: relative;
    margin-bottom: 20px;
    padding-left: 15px;
    color: #fff;
    text-decoration: none;
}

.dialogs .users .item::before {
    content: '';
    position: absolute;
    top: 50%;
    left: 0;
    transform: translateY(-50%);
    width: 6px;
    height: 6px;
    border-radius: 50%;
    background-color: #fff;
}

.dialogs .users .item.active {
    color: gold;
}

.dialogs .users .item.active::before {
    background-color: gold;
}

.dialogs .body {
    width: 100%;
    padding-left: 50px;
}

.dialogs .messages .item {
    display: flex;
    align-items: center;
    font-size: 20px;
    line-height: 24px;
    margin-bottom: 20px;
}

.dialogs .messages .item img {
    width: 50px;
    height: 50px;
    border-radius: 50%;
    object-fit: cover;
    margin-right: 30px;
}

.dialogs .messages .item span {
    padding: 10px;
    border: 1px solid #fff;
    border-radius: 15px;
}

.mess__form {
    width: 100%;
    margin-top: 30px;
}

.mess__area {
    width: 100%;
    height: 100px;
    resize: none;
    font-size: 22px;
    line-height: 25px;
    padding: 10px;
    margin-top: 10px;
    box-sizing: border-box;
}

.mess__btn {
    cursor: pointer;
    display: block;
    padding: 10px;
    margin-left: auto;
    background-color: green;
    color: #fff;
}
